import Bmob from "hydrogen-js-sdk";
import print from "@/utils/print.js"

import loading from "@/components/Loading/index.vue"
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'

let that;
let id;
export default {
	components: {
		loading,
		uniNavBar
	},
	data() {
		return {
			user: uni.getStorageSync("user"),
			bills: [],
			loading: true,
			products: null,
			detail: null,
		}
	},
	onLoad(options) {
		console.log(options)
		that = this;
		id = options.id;
		that.getdetail(id);
	},
	methods: {

		gotoexpressDet() {
			if (that.user.is_vip) {
				uni.navigateTo({
					url: "../expressDet/expressDet?number=" + that.detail.expressNum
				})
			} else {
				uni.showToast({
					title: "您还不是会员，无法使用",
					icon: 'none'
				})
			}
		},

		//预览图片
		priview(url) {
			uni.previewImage({
				current: url,
				urls: that.detail.Images,
			});
		},

		//点击显示操作菜单
		show_options() {
			let options = ['打印'];
			if (that.detail.type == -1 || that.detail.type == 1) {
				options = ['审核', '撤销', '打印']
			}
			uni.showActionSheet({
				itemList: options,
				success: function(res) {
					if (res.tapIndex == 0) {
						if (that.detail.status) {
							uni.showToast({
								title: "该笔采购单已审核",
								icon: "none"
							})
						} else {
							that.confrimOrder()
						}
						
						uni.setStorageSync("is_option",true)
					} else if (res.tapIndex == 1) {
						that.revoke()
						uni.setStorageSync("is_option",true)
					} else if (res.tapIndex == 1) {
						print.print_operations(that.detail, that.products)
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		getdetail: function(id) {
			const query = Bmob.Query('order_opreations');
			query.include("opreater", "custom", "producer", "stock");
			query.get(id).then(res => {
				console.log(res);
				that.detail = res;
				that.products = res.detail;
				that.bills = res.bills;
				that.loading = false;
			}).catch(err => {
				console.log(err)
			})
		},

		//数据撤销点击
		revoke: function() {
			wx.showModal({
				title: '提示',
				content: '数据撤销后不可恢复，请谨慎撤销！',
				success(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '撤销中...'
						})
						const query = Bmob.Query('order_opreations');
						query.destroy(that.detail.objectId).then(res => {
							const query = Bmob.Query('Bills');
							query.containedIn("objectId", that.bills);
							query.find().then(todos => {

								todos.destroyAll().then(res => {
									// 成功批量修改
									for (var i = 0; i < that.products.length; i++) {
										that.delete_bill(i);
									}
								}).catch(err => {
									console.log(err)
								});
							})
						}).catch(err => {
							console.log(err)
						})
					}
				}
			})
		},

		//审核订单
		confrimOrder() {
			wx.showModal({
				title: '提示',
				content: '是否审核该笔操作单！',
				success(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '审核中，请勿退出该页面...'
						})
						const query = Bmob.Query('order_opreations');
						query.set('id', id) //需要修改的objectId
						query.set('status', true)
						query.save().then(res => {
							console.log(res)
							let count = 0
							for (let item of that.products) {
								that.addOrReduceGoodReserve(item, count);
								count += 1;
							}
						}).catch(err => {
							console.log(err)
						})
					}
				}
			})
		},

		//确定审核之后改变产品库存
		addOrReduceGoodReserve(product, count) {
			const query1 = Bmob.Query('Goods');
			query1.set('id', product.goodsId.objectId);
			if(product.goodsId.selectd_model){
				let num = 0;
				for (let model of JSON.parse(product.goodsId.selectd_model)) {
					for (let item of product.goodsId.models) {
						num += Number(product.goodsId.reserve)
						if (item.id == JSON.parse(model).id) {
							item.reserve = Number(item.reserve) + Number(product.num)
						}
					}
				}
				query1.set('models', product.goodsId)
				query1.set('reserve', num);
			}else{
				query1.set('reserve', product.goodsId.reserve);
			}
			
			query1.save().then(res => {
				if (count == (that.products.length - 1)) {
					const query = Bmob.Query('Bills');
					query.containedIn("objectId", that.bills);
					query.find().then(todos => {
						todos.set('status', true);
						todos.saveAll().then(res => {
							uni.hideLoading();
							uni.navigateBack({
								delta: 1
							})
							setTimeout(function() {
								uni.showToast({
									title: '审核成功'
								})
							}, 1000);
							console.log(res, 'ok')
						}).catch(err => {
							console.log(err)
						});
					})
				}
			})
		},

		delete_bill: function(i) {
			let product = that.products[i];
			let bill = that.bills[i]

			const query1 = Bmob.Query('Goods');
			query1.set('id', product.goodsId.objectId);
			if (product.type == 1) {
				query1.set('reserve', product.goodsId.reserve - product.num);
			} else if (product.type == -1) {
				query1.set('reserve', product.goodsId.reserve + product.num);
			}
			query1.save().then(res => {
				if (i == (that.products.length - 1)) {
					uni.hideLoading();
					uni.navigateBack({
						delta: 1
					})
					setTimeout(function() {
						uni.showToast({
							title: '撤销成功'
						})
					}, 1000);
				}
			})
		}

	}
}
